export const StyledTypographyComponent = styled.div`
  font-family: 'Rubik', sans-serif;
  margin-bottom: 10px;
  color: ${theme.textLinkColors};

  ${({ type }) => {
    switch (type) {
      case 'h1':
        return css`
          font-size: 48px;
          font-weight: 800;
          height: 50.3984px;
          line-height: 50.4px;
        `;
      case 'h2':
        return css`
          font-size: 36px;
          font-weight: 700;
          height: 45px;
        `;
      case 'h3':
        return css`
          font-size: 28px;
          font-weight: 600;
          height: 35px;
        `;
      case 'h4':

      case 'h5':
        return css`
          font-size: 1.8em;
          font-weight: bold;
        `;
      default:
        return '';
    }
  }}
`;
